import React from "react";
import "./Register.css";
import { BrowserRouter, Link } from "react-router-dom";

const Register = () => {
  return (
    <div className="register-container">
      <div className="register-wrapper">
        
        <div className="register-right">
          <div className="logo">
            <img
              src="data:image/png;base64,"
              alt="Crystal Delta Logo"
              className="logo-image"
            />
          </div>
          <h2>Welcome Back!</h2>
          <form className="register-form">
            <input type="text" id="username" placeholder="Username" />
            <input type="email" id="email" placeholder="Email" />
            <input type="password" id="password" placeholder="Password" />
            <input type="password" id="confirmpassword" placeholder="ConfirmPassword" />
            <button type="submit" className="register-btn">
              Register
            </button>
          </form>
          <p className="login-link">
           continue without an account<Link to="/home"><button >Go to Home</button></Link>
          </p>
        </div>
        <div className="register-left">
          <img
            src="https://us.123rf.com/450wm/freeslab/freeslab2107/freeslab210700148/211524120-family-cycling-together-in-park-flat-color-vector-faceless-characters-mother-father-and-son-on.jpg?ver=6"
            alt="Fitness Tracker Illustration"
            className="illustration"
          />
        </div>
      </div>
    </div>
  );
};export default Register;